import * as React from "react";
import { getBoardData, updateBoardData } from "../utils/api-routes/api-routes.util";
import { useState } from "react";
import * as jsoncompare from 'js-object-compare';

interface IContextProps {
  dashboard: boolean;
  dispatchGetBoardDetails: Function;
  boards: any;
  dispatchUpdateBoardDetails: Function;
  onBoardDataChange: Function;
  boardEventBus: any;
  setBoardEventBus: Function;
  addBoardLane: Function;
}

export const AppContext = React.createContext({} as IContextProps);

export default ({ children }) => {
  const dashboard = false;
  const [boards, setBoards] = useState({});
  let boardEventBus;
  const setBoardEventBus = (handle) => {
    boardEventBus = handle
  }



  const  formatBoards= (data) => {
    const boardsData = data.map((d) => {
      //console.log(d.data);
      d.orignaldata = d.data;
      d.data = JSON.parse(d.data);
      d.data.lanes = d.data.lanes.map((l) => {
        // l.cards = l.card ? l.cards : [];
        return l;
      });
      const rd = {};
      rd[d.name] = d.data;
      // TODO: fix this
      return d;
    });
    //console.log(boardsData);
    return boardsData;
  }

  const addBoardLane = (data: any) => {
    const laneData = boards[0].data.lanes;
    laneData.push({
      id: 'lane1'+Math.random(),
      title: data.title,
      label: '',
    });
    boardEventBus.publish({
      type: 'UPDATE_LANES',
      lanes: laneData,
    })
  }


  const onBoardDataChange = (newData) => {
    if (!jsoncompare.sameContent(newData, JSON.parse(boards[0].orignaldata))) {
      newData.lanes = newData.lanes.map((lane,i) => {
        if (lane.cards === undefined) {
          lane.cards = [];
        } else{
          // lane.cards = lane.cards.filter((card) => {
          //   return !card.isEmpty;
          // })
        }
        return lane;
      });
      dispatchUpdateBoardDetails({
        boardId: boards[0].id,
        newBoardData: JSON.stringify(newData),
        oldBoardData: boards[0].orignaldata,
      });
    }
  }


  const dispatchGetBoardDetails = () => {
    getBoardData({}).subscribe(
      (response: any) => {
        if (response.response.Requested_Action) {
          setBoards(formatBoards(response.response.data));
        } else {
          //console.log(response);
        }
        // //console.log(response);
      },
      response => {
        //console.log(response);
      }
    );
  };

  const dispatchUpdateBoardDetails = (payload) => {
    updateBoardData(payload).subscribe(
      (response: any) => {
        if (response.response.Requested_Action) {
          // setBoards(formatBoards(response.response.data));
          dispatchGetBoardDetails();
        } else {
          //console.log(response);
        }
        // //console.log(response);
      },
      response => {
        //console.log(response);
      }
    );
  };

  const defaultContext = {
    dashboard,
    dispatchGetBoardDetails,
    boards,
    dispatchUpdateBoardDetails,
    onBoardDataChange,
    boardEventBus,
    setBoardEventBus,
    addBoardLane,
  };

  return (
    <AppContext.Provider value={defaultContext}>{children}</AppContext.Provider>
  );
};
