import * as React from 'react';
import menubanner from '../../assets/menubanner.svg';
import plus from '../../assets/plus.svg';
import { Dropdown } from 'react-bootstrap';
import { useState } from 'react';

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
    <a
      href=""
      ref={ref}
      onClick={e => {
        e.preventDefault();
        onClick(e);
      }}
    >
      {children}
    </a>
  ));
  
  // forwardRef again here!
  // Dropdown needs access to the DOM of the Menu to measure it
  const CustomMenu = React.forwardRef(
    ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {
      const [value, setValue] = useState('');
  
      return (
        <div
          ref={ref}
          style={style}
          className={className}
          aria-labelledby={labeledBy}
        >
          <ul className="list-unstyled">
            {React.Children.toArray(children).filter(
              child =>
                !value || child.props.children.toLowerCase().startsWith(value),
            )}
          </ul>
        </div>
      );
    },
  );

export function LaneHeader(prop) {
    // //console.log(prop);
    return (
        <div className="lane_header">
            <div className="row">
                <div className="col">
                    {prop.title}
                </div>

                <div className="col text-right">
                    <Dropdown>
                            <Dropdown.Toggle as={CustomToggle} id="dropdown-alerts">
                                <img src={menubanner} alt="" />
                            </Dropdown.Toggle>

                            <Dropdown.Menu as={CustomMenu} alignRight="true" className="create_card_dropdown_menu">
                                <li><Dropdown.Item eventKey="1" href="#"><img src={plus} alt="" /> <span>Create Card</span></Dropdown.Item></li>
                            </Dropdown.Menu>
                        </Dropdown>
                </div>
            </div>
        </div>
    );
}
